.nav {
  padding: 1rem;
  background: var(--core-gray);
  color: var(--color-text-light);
}
.nav a {
  text-decoration: none;
  color: inherit;
}
.nav .nav-li-active > a {
  color: var(--core-violet-4);
}

.nav-component {
  line-height: 1rem;
}

.nav-component a,
.btn-back {
  padding: 0.7rem 1rem;
}

.nav-title {
  padding: 0 1rem;
  font-size: 0.8em;
  font-family: var(--heading);
  color: var(--aluminum-5);
  letter-spacing: 0.05em;
  font-weight: var(--weight-bold);
