}

.btn-back {
  opacity: 0.6;
  margin-bottom: 0.5rem;
  line-height: 1.5rem;
  margin-left: 1.3rem;
  cursor: pointer;
}
.btn-back::before {
  display: inline-block;
  content: url(../images/icons/arrow_back.svg);
  margin-right: 0.3em;
  margin-left: -1.3rem;
  width: 1rem;
}

.nav-heading {
  font-size: 1.3rem;
  font-weight: var(--weight-medium);
