button.collapse-toggle {
  cursor: pointer;
  padding: 0;
  border: none;
}
button.collapse-toggle.toggled img {
  transform: rotate(-90deg);
}
button.collapse-toggle.toggled ~ .collapsible-content {
  display: none;
}

.close-menu-btn {
  border: 0;
}

.component-version {
  position: relative;
}

.btn-version {
  font-size: 0.85rem;
  font-weight: var(--weight-normal);
  min-height: 1.5rem;
  padding: 0 0.5rem;
  margin: 0.4rem 0;
  border: 1px solid;
  border-radius: 1rem;
}
.btn-version::after {
  display: inline-block;
  content: url(../images/icons/chevron.svg);
  width: 0.8rem;
  margin-left: 0.2rem;
}
.btn-version:hover, .btn-version:focus {
  color: var(--core-violet-4);
}
.btn-version svg {
  height: var(--md);
  width: var(--md);
}

.popover-container {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 100%;
  z-index: 1;
  padding-top: 0.5em;
}
.btn-version:focus + .popover-container, .popover-container:focus-within {
  opacity: 1;
  pointer-events: auto;
}

.popover {
  font-family: var(--sans-serif);
  font-size: 0.9em;
  background: var(--tertiary);
  border: 1px solid var(--aluminum-3);
  border-radius: var(--border-radius);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  color: var(--steel-3);
  padding: 0.5em;
  margin: 0 var(--xs);
  width: 10em;
  user-select: none;
}
.popover p {
  margin: 0;
  padding: 0 0.5em;
  font-weight: bold;
  font-size: 0.9em;
}
.popover ul {
  margin: 0;
  padding: 0;
}
.popover ul li {
  list-style: none;
}
.popover ul li a {
  display: block;
  padding: 0.2em 0.5em;
}
.popover ul li a:hover {
  background-color: var(--aluminum-2);
}

/* These are hidden from the site and only shown in the READMEs on GitHub. */
.readme-notice {
  display: none;
}

.root {
  display: flex;
}

.nav {
  height: 100vh;
  overflow-y: auto;
  position: sticky;
  top: 0;
  width: 300px;
  flex: 0 0 auto;
}

.nav-heading {
  margin: 0.75rem 0;
  align-items: center;
}

.nav-component-active {
  min-height: 100%;
  overflow-y: auto;
}

.nav-component-active .nav-heading-active {
  position: sticky;
  top: 0;
  margin-top: 0;
}

.nav-heading-active {
  position: sticky;
  top: 15px;
}

.nav-component-heading + .nav-list {
